import React, { Component } from 'react'
import './projects.css';
import { ProjectTile } from './ProjectTile';
import { ViewProject } from './ViewProject'

let tileSpecs = {
  top: '',
  left: '',
  width: '',
  height: '',
}

let gridSpecs = {
  top: '',
  left: '',
  width: '',
  height: '',
}
let tileDiv = document.querySelector('.tile')



export class ProjectShow extends Component {
  constructor(props) {
    super(props)
    this.gridExpand = this.gridExpand.bind(this)
    this.handleClick = this.handleClick.bind(this)
    this.handleProjectViewToggle = this.handleProjectViewToggle.bind(this)
    this.svgShow = this.svgShow.bind(this)
    this.svgHide = this.svgHide.bind(this)
    this.state = {
      toggle: false,
      projectViewToggle: false,
      svgToggle: false,
      currentLetter: 'a',
      currentTitle: '',
      tileFull: false,
      // interruptedAnim: {
      //   wasInterrupted: false,
      //   initialWidth: '',
      //   endWidth: '',
      //   interruptionPoint: '',

      // }
    }
  }
  
  
  
  
      
  handleClick(){ 
        const currentState = this.state.toggle;
        this.setState({ toggle: !currentState })
      }
  
  handleHover() {
    const currentState = this.state.svgToggle;
    this.setState({ svgToggle: !currentState })
      }
  handleProjectViewToggle() {
    const currentState = this.state.projectViewToggle;
    this.setState({ projectViewToggle: !currentState })
      }
  
  gridRetract(e) {
    let tileDiv = document.querySelector('.tile')
    let projectsWrapper = document.querySelector('#projectsWrapper')
    let contentWrapper = document.querySelector('.contentWrapper')
    
    document.documentElement.style
    .setProperty('--tile-top', `${tileSpecs.top}px`);
    document.documentElement.style
    .setProperty('--tile-left', `${tileSpecs.left}px`);
    document.documentElement.style
    .setProperty('--tile-width', `${tileSpecs.width}px`);
    document.documentElement.style
    .setProperty('--tile-height', `${tileSpecs.height}px`);
    
    tileDiv.className = `${this.state.currentLetter} tile retract`
    // if(tileDiv.offsetWidth >= 1200) {
    //   this.
    // }

    this.handleProjectViewToggle()


    tileDiv.addEventListener('animationend', reset)
    function reset() {
      contentWrapper.style.display = 'none';
      projectsWrapper.style.display = 'block'

      tileDiv.removeEventListener('animationend', reset)
    }
  }

  gridExpand(e) {
    let tile;
    // preventing bubbling of click to h2 element
    e.target.classList[0].length === 1 ? (tile = e.target) : (tile = e.target.parentElement);
    
    let gridLetter = tile.classList[0];
    let grid = document.querySelector('.grid')
    let tileDiv = document.querySelector('.tile')
    let contentWrapper = document.querySelector('.contentWrapper')
    
    this.setState({ currentLetter: gridLetter })
    
    
    tileSpecs.top = tile.offsetTop;
    tileSpecs.left = tile.getBoundingClientRect().left;
    tileSpecs.width = tile.getBoundingClientRect().width;
    tileSpecs.height = tile.getBoundingClientRect().height;
    
    gridSpecs.top = grid.offsetTop
    gridSpecs.left = grid.getBoundingClientRect().left;
    gridSpecs.width = grid.getBoundingClientRect().width
    gridSpecs.height = grid.getBoundingClientRect().height
    
    function handleInterruption() {
      let wasInterrupted = this.state.interruptedAnim.wasInterrupted;
      let interruptionPoint =  this.state.interruptedAnim.interruptionPoint;
      let initialWidth =  tileSpecs.width;
      let endWidth =  gridSpecs.width;
    }
    
// Animation begins

    contentWrapper.style.display = 'block'

    // projectsWrapper.style.display = 'none'
    
    
    tileDiv.className = `${gridLetter} expand tile`
    
    contentWrapper.style.width = gridSpecs.width + 'px'
    
    tileDiv.style.top = tileSpecs.top + 'px'
    tileDiv.style.left = tileSpecs.left + 'px'
    tileDiv.style.width = tileSpecs.width + 'px'
    tileDiv.style.height = tileSpecs.height + 'px'
    
    document.documentElement.style
    .setProperty('--grid-top', `${gridSpecs.top}px`);
    document.documentElement.style
    .setProperty('--grid-left', `${gridSpecs.left}px`);
    document.documentElement.style
    .setProperty('--grid-width', `${gridSpecs.width}px`);
    document.documentElement.style
    .setProperty('--grid-height', `${gridSpecs.height}px`);
    
    // interruption check
//     let currentWidth = parseInt(tileDiv.style.width)
//     this.setState({ 
//       interruptedAnim: {
//         interruptionPoint: currentWidth
//       }
// })
// tileDiv.addEventListener('onchange', console.log(tileDiv.style.width))

let anim = () => {
  if (tileDiv.offsetWidth === (1200)) {
    this.handleProjectViewToggle()
    tileDiv.removeEventListener('animationend', anim)
  }
  
    }
    
    tileDiv.addEventListener('animationend', anim)
}

svgShow() {
  this.setState({svgToggle : true})
}
svgHide() {
  this.setState({svgToggle : false})
}
    
    render() {
        return (
          <div> 
          <div id='projectsWrapper'>
                <div 
                className='grid unclicked'>
                  <ProjectTile
                    getLetter={this.getLetter}
                    gridExpand={this.gridExpand}
                    gridLetter='a'
                    projectTitle='Literary Timeline from Raw Data'
                  />
                  <ProjectTile
                    getLetter={this.getLetter}
                    gridExpand={this.gridExpand}
                    gridLetter='b'
                    projectTitle='Animal Sounds'
                  />
                  <ProjectTile
                    getLetter={this.getLetter}
                    gridExpand={this.gridExpand}
                    gridLetter='c'
                    projectTitle='Coming Soon'
                  />
                  <ProjectTile
                    getLetter={this.getLetter}
                    gridExpand={this.gridExpand}
                    gridLetter='d'
                    projectTitle='Personality Traits - Flexbox visual'
                  />
                  <ProjectTile
                    getLetter={this.getLetter}
                    gridExpand={this.gridExpand}
                    gridLetter='e'
                    projectTitle='Coming Soon'
                  />
                  <ProjectTile
                    getLetter={this.getLetter}
                    gridExpand={this.gridExpand}
                    gridLetter='f'
                    projectTitle='Coming Soon'
                  />
                  <ProjectTile
                    getLetter={this.getLetter}
                    gridExpand={this.gridExpand}
                    gridLetter='g'
                    projectTitle='Coming Soon'
                  />
              </div>
          </div>
        <div className='contentWrapper'>
           <div className='tile'
           onClick={(e) => (e.target.offsetWidth === 1200) ? this.gridRetract() : null}
           >
            {this.state.projectViewToggle ? 
            <ViewProject
            svgShow={this.svgShow}
            svgHide={this.svgHide}
            svgToggle={this.state.svgToggle}
            gridLetter={this.state.currentLetter}
            projectTitle={this.state.currentTitle}
            />
            : null
            }
           </div>
        </div>
        </div>
        )
    }
}

export default ProjectShow;
