import React from 'react';
import './skills.css';
import Skillbar from './Skillbar'

export class Skills extends React.Component {
    constructor(props) {
        super(props)
    
        this.state = {
             isInViewport: false,
        }
    this.handleSroll=this.handleSroll.bind(this)
    }
    componentDidMount() {
        window.addEventListener('resize', this.handleSroll)
        window.addEventListener('scroll', this.handleSroll)
    }
    
    handleSroll(){
         // Skillbar animation when fully in viewport  
        
        let skillNodeList = document.querySelectorAll('.skillbar')
        let skillArray = Array.from(skillNodeList)
        

         function isFullyInViewport(el) {
            let elementTop = el.offsetTop;
            let elementBottom = elementTop + el.offsetHeight;
            let viewportTop = window.scrollY
            let viewportBottom = viewportTop + window.innerHeight
             return elementTop >= viewportTop && elementBottom <= viewportBottom;
             

         }
        function findSkill() {
            let skillInView
         for (let i = 0; i < skillArray.length; i++) {
             if (isFullyInViewport(skillArray[i])) {
                skillInView = skillArray[i]
                 
             }
         }
         if (skillInView !== undefined){
             let bar = skillInView.children[1]
             let data = skillInView.getAttribute('data-percent')
            bar.style.width = data;
         }
        }
        findSkill()
//    $.fn.isFullyInViewport = function() {
//     var elementTop = $(this).offset().top;
//     var elementBottom = elementTop + $(this).outerHeight();
  
//     var viewportTop = $(window).scrollTop();
//     var viewportBottom = viewportTop + $(window).height();
  
//     return elementTop >= viewportTop && elementBottom <= viewportBottom;
//   };
  
//   $(window).on('resize scroll', function() {
    
//     $('.skillbar').each(function(){
//       if ($(this).isFullyInViewport()) {
//       $(this).find('.skillbar-bar').animate({
//         width:$(this).attr('data-percent')
//       },1000);
//     }
//     });
//   });     
//  })
    }

    render() {
        return (
        <div id='skillsWrapper'>
            <Skillbar
            percent = '90%'
            titleColor = '#d35400'
            icon = "devicon-html5-plain-wordmark"
            name = 'HTML5'
            barColor = '#e67e22'
            />
            <Skillbar 
            percent = '70%'
            titleColor = '#2980b9'
            icon = "devicon-css3-plain-wordmark"
            name = 'CSS3'
            barColor = '#3498db'
            />
            <Skillbar
            percent = '50%'
            titleColor = '#90832f'
            icon = "devicon-javascript-plain"
            name = 'JavaScript'
            barColor = '#f0db4f'
            />
            <Skillbar 
            percent = '60%'
            titleColor = '#2c3e50'
            icon = "devicon-jquery-plain-wordmark"
            name = 'JQuery'
            barColor = '#2c3e50'
            />
            <Skillbar
            percent = '50%'
            titleColor = '#008199'
            icon = 'devicon-react-original-wordmark'
            name = 'React.js'
            barColor = '#00d8ff'
            />
            <Skillbar
            percent = '30%'
            titleColor = '#782817'
            icon = "devicon-git-plain-wordmark"
            name = 'Git'
            barColor = '#f1502f'
            />
        </div>
        )
    }
}