import React from 'react'
import { Info } from './Info/Info'

const wrapStyles = {
    height: 60,
    width: '100%',
    backgroundColor: '#dad878',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    padding: '30px 0px',
    border: '2px solid',
    margin: '20px 0px'
}
const headerStyles = {
    color: '#111',
    fontFamily: 'Open Sans Condensed, sans-serif',
    fontSize: 64,
    fontWeight: '700',
    textShadow: '5px 4px 2px rgb(161, 161, 161)',
    margin: '0 0 0',
    textAlign: 'center',
    textTransform: 'uppercase',
    position: 'relative',
}

export const Header = ({name}) => {
    return (
        <div className={name
            .toLowerCase()
            .replace(/\s/g, '')
            + 'Wrapper'}
        style={wrapStyles}
        >           
            <h2 
            className='header2'
            style={headerStyles}
            >{name}</h2>
        </div>
    )
}

export default Header
