import React, { Component } from 'react'
import './info.css'

export class Info extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
        }
        this.handleMouseOver = this.handleMouseOver.bind(this)
    }

    handleMouseOver(e) {
        function typeEffect(element, speed) {
            var text = element.innerHTML;
            element.innerHTML = "";
            
            var i = 0;
            var timer = setInterval(function() {
            if (i < text.length) {
              element.append(text.charAt(i));
              i++;
            } else {
              clearInterval(timer);
            }
          }, speed);
        }
        
        
        // application
        var speed = 75;
        var h1 = document.querySelector('.infoHeader');
        var p = document.querySelector('.infoText');
        var delay = h1.innerHTML.length * speed + speed;
        
        // type affect to header
        typeEffect(h1, speed);
        
        
        // type affect to body
        setTimeout(function(){
          p.style.display = "inline-block";
          typeEffect(p, speed);
        }, delay);
        
    }
    render() {
        return (
            <div className='infoWrapper' >
                <div
                className='info'
                onMouseOver={this.handleMouseOver}
                >
                </div>
                <div className="infoContent">
                    <h1 className='infoHeader'>A Work in Progress</h1>
                    <p className='infoText'>My projects are a work in progress, but I do like them</p>
                </div>
            </div>
        )
    }
}

export default Info
