import React, { Component } from 'react'

const wrapStyles = {
  position: 'relative',
  top: 0,
  margin: 'auto',
  paddingTop: 30,
  marginTop: -40,
  padding: '25px 0px',
  width: '100%',
  height: '100%',
  textAlign: 'center',
  background: '#dad878',
}

const textStyles = {
  display: 'inline-block',
  font: '400 50px/1.3 "Oleo Script", Helvetica, sans-serif',
  color: '#2b2b2b'
}


export class AboutMe extends Component {
  render() {
    return (
      <div id='aboutMeWrapper' style={wrapStyles}>
        <p style={textStyles}>Hello, I'm Jens - a frontend developer from Aarhus, Denmark</p>
      </div>
    )
  }
}

export default AboutMe
